export default {
	data() {
		return {
			list:"",
			id:0,
			other:""
		}
	},
	methods: {
		look:function(user,time){
			const vue=this;
			uni.removeStorage({
				key:"remark"
			})
			
			uni.navigateTo({
				url:"../remark/remark?user="+user+'&time='+time,
				success:function(){
					uni.setStorage({
						key:"remark",
						data:{user:user,time:time,other:vue.other}
					})
				}
			})
		},
		talk:function(uadmin,uinfo,utime){
			const vue=this
			uni.removeStorage({
				key:"id"
			})

			const admin={
				user:uadmin,
				info:uinfo,
				time:utime,
				other:vue.other
			}
			uni.navigateTo({
				url:"../talk/talk?user="+uadmin+'&info='+uinfo+'&time='+utime+'&other='+vue.other,
				success:function(){
					uni.setStorage({
						key:"id",
						data:{user:uadmin,info:uinfo,time:utime,other:vue.other}
					})
				}
			})

			


































		},
		all:function(){
			// uni.showToast({
			// 	title:"加载中",
			// 	duration:1500
			// })
			const vue=this;
			this.id=this.id+1;
			uni.showLoading({
				title:"加载中",
				success() {
					setTimeout(()=>{
						uni.hideLoading()
						uni.request({
										url:"https://25q686n876.qicp.vip/tp5/publica/index/Bii/xin",
										method:"POST",
										data:{id:vue.id},
										dataType:"json",
										header:{
											"Content-Type":"application/x-www-form-urlencoded"
										},
										success:function(res){
											
											vue.list=res.data;
										}
									})
					},1500)
				}
			})
			
			}
	},
	onLoad() {
		const vue=this;
		uni.getStorage({
							key:"user",
							success:function(res){
								vue.other=res.data
								console.log(vue.other)
							}
						})
		const data={
			id:0
		}
		uni.request({
			url:"https://25q686n876.qicp.vip/tp5/publica/index/Bii/xin",
			method:"POST",
			data:data,
			dataType:'json',
			header:{
				"Content-Type":"application/x-www-form-urlencoded"
			},
			success:function(res){
				
				vue.list=res.data;
			}
		})
	},
	onPullDownRefresh:function(){
		console.log(123)
		const vue=this;
		setTimeout(()=>{
			uni.stopPullDownRefresh();
			uni.request({
				url:"https://25q686n876.qicp.vip/tp5/publica/index/Bii/xin",
				method:"POST",
				header:{
					"Content-Type":"application/x-www-form-urlencoded"
				},
				success:function(res){
					
					vue.list=res.data;
				}
			})
		},1500)
	}
}
